import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'

import ArrowBackIcon from '@mui/icons-material/ArrowBack'
import { Box, Button, Card, Container, Divider, type Theme, Typography, useMediaQuery } from '@mui/material'

import PageTitleWrapper from 'src/components/PageTitleWrapper'

import HonoraryMemberGrid from 'src/content/manage/Member/HonoraryGrid'
import PageHeader from 'src/content/manage/PageHeader'

import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

function Member() {
  const navigate = useNavigate()
  const { id } = useParams()
  const { userInfo } = useSafeAuthUser()
  const matches = useMediaQuery((theme: Theme) => theme.breakpoints.up('sm'))

  const clubId = Number(id)
  const club = userInfo?.clubs.find(club => club.id === clubId)

  return (
    <>
      <Helmet>
        <title>{club?.name ?? ''} 회원 명단 - KLUB</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club?.name ?? ''} />
      </PageTitleWrapper>
      <Container style={{ width: '100%' }} sx={{ px: matches ? 2 : 0 }}>
        {/* <DateForm club={club} /> */}
        <Card>
          <Box display="flex" alignItems="baseline" flexDirection="row" sx={{ alignItems: 'center' }}>
            <Button
              variant="text"
              onClick={() => {
                navigate(-1)
              }}
              color="primary"
            >
              <ArrowBackIcon />
            </Button>
            <Box display={'flex'} alignItems={'baseline'}>
              <Typography variant="h4" paddingY={2}>
                명예회원 명단
              </Typography>
              {/* <Typography variant={'caption'}>명예회원으로 등록된 인원</Typography> */}
            </Box>
          </Box>
          <Divider />
          <HonoraryMemberGrid />
        </Card>
      </Container>
    </>
  )
}

export default Member
