import { useSnackbar } from 'notistack'
import { useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Navigate, useParams } from 'react-router'

import LocalPoliceIcon from '@mui/icons-material/LocalPolice'
import ManageSearchIcon from '@mui/icons-material/ManageSearch'
import ShieldIcon from '@mui/icons-material/Shield'
import VerifiedIcon from '@mui/icons-material/Verified'
import { Box, type Theme, Tooltip, Typography, useMediaQuery } from '@mui/material'
import type { GridColDef, GridRenderCellParams, GridRowParams, GridRowSelectionModel } from '@mui/x-data-grid'
import { DataGrid, GRID_CHECKBOX_SELECTION_COL_DEF, GridActionsCellItem } from '@mui/x-data-grid'

import { CLUB_MEMBER_MESSAGE } from 'src/data/messages'

import type { ClubMemberForPatch } from 'src/models/clubMember'
import { type ClubMember } from 'src/models/clubMember'

import ConfirmDialog from 'src/components/ConfirmDialog'

import CustomGridToolbar from 'src/content/manage/Member/MemberGrid/CustomGridToolbar'
import DetailsModal from 'src/content/manage/Member/MemberGrid/DetailsModal'

import { getMemberExcelAPICall, useDeleteMembers, useGetHonoraryMembers, usePatchMembers } from 'src/hooks/api/member'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

import 'moment/locale/ko'

