const HonoraryMemberGrid = () => {
  const { id } = useParams()
  const clubId = Number(id)

  const matches = useMediaQuery((theme: Theme) => theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()

  const { userInfo } = useSafeAuthUser()
  const authHeader = useAuthHeader()

  const { data: unsafeTypeMmembers } = useGetHonoraryMembers(clubId)
  const members = unsafeTypeMmembers ?? []
  const updateMembersMutation = usePatchMembers()
  const deleteMemberMutation = useDeleteMembers()

  const [modalMembers, setModalMembers] = useState<ClubMember[]>([])
  const [selectedMemberIds, setSelectedMemberIds] = useState<GridRowSelectionModel>([])
  const [isDeleteModal, setIsDeleteModal] = useState<boolean>(false)

  const club = userInfo?.clubs.find(({ id }) => id === clubId)
  const selectedMembers = members.filter(({ id }) => selectedMemberIds.includes(id))
  const isAllMembersNotBelongToClub = selectedMembers.every(({ withdrawalAt, firedAt }) => !!withdrawalAt || !!firedAt)

  const handleRowSelectionChange = (selectedMemberIds: GridRowSelectionModel) => {
    setSelectedMemberIds(selectedMemberIds)
  }

  const handleUpdatedMembers = async (updatedMembers: ClubMember[]) => {
    try {
      const updatedMembersIdToUserId: ClubMemberForPatch[] = updatedMembers.map(member => {
        const { id, ...rest } = member
        return { ...rest, userId: id }
      })
      await updateMembersMutation.mutateAsync({ clubId, updatedMembers: updatedMembersIdToUserId })
      setModalMembers([])
    } catch (error) {}
  }

  const handleSearchButtonClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    setModalMembers(selectedMembers)
  }

  const handleDeleteButtonClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    setIsDeleteModal(true)
  }

  const handleDeleteMembers = () => {
    const deletedMembers = selectedMembers.map(member => ({
      userId: member.id,
      semesterInfo: { semester: member.semester, year: member.year }
    }))
    deleteMemberMutation.mutate({ clubId, deletedMembers })
    setIsDeleteModal(false)
  }

  const handleExportButtonClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    getMemberExcelAPICall(clubId, authHeader())
      .then(({ data }) => {
        const blob = new Blob([data])
        const fileObjectUrl = window.URL.createObjectURL(blob)

        const link = document.createElement('a')
        link.href = fileObjectUrl
        link.style.display = 'none'

        link.download = `${club?.name}.xlsx`

        document.body.appendChild(link)
        link.click()
        link.remove()

        window.URL.revokeObjectURL(fileObjectUrl)
      })
      .catch(error => {
        enqueueSnackbar(error?.response?.data?.message ?? CLUB_MEMBER_MESSAGE.SERVER_ERROR, { variant: 'error' })
      })
  }

  const columns: GridColDef[] = [
    {
      ...GRID_CHECKBOX_SELECTION_COL_DEF,
      headerName: '선택'
    },
    {
      field: 'actions',
      type: 'actions',
      headerName: '상세',
      width: 50,
      getActions: (params: GridRowParams<ClubMember>) => [
        <Tooltip title="클릭하면 회원 정보를 더 자세하게 확인할 수 있습니다.">
          <GridActionsCellItem
            icon={<ManageSearchIcon />}
            label="자세히 보기"
            onClick={() => setModalMembers([params.row])}
          />
        </Tooltip>
      ]
    },
    {
      field: 'name',
      headerName: '이름',
      width: 140,
      renderCell: (params: GridRenderCellParams<ClubMember>) => (
        <>
          <Typography sx={{ maxWidth: '80px', overflow: 'hidden', textOverflow: 'ellipsis' }}>
            {params.value}
          </Typography>
          {params.row.isAuthenticated && (
            <Tooltip title="KLUB에서 인증 완료된 회원입니다.">
              <VerifiedIcon color="primary" fontSize="small" sx={{ marginLeft: '2px' }} />
            </Tooltip>
          )}
          {params.row.permission == 'SUPERADMIN' && (
            <Tooltip title="최고 운영진 권한을 가진 회원입니다. KLUB에서 제공하는 운영진 기능을 모두 이용할 수 있습니다.">
              <LocalPoliceIcon color="warning" fontSize="small" sx={{ marginLeft: '2px' }} />
            </Tooltip>
          )}
          {params.row.permission == 'ADMIN' && (
            <Tooltip title="일반 운영진 권한을 가진 회원입니다. 권한 변경을 제외한, KLUB에서 제공하는 운영진 기능을 모두 이용할 수 있습니다.">
              <ShieldIcon color="warning" fontSize="small" sx={{ marginLeft: '2px' }} />
            </Tooltip>
          )}
        </>
      )
    },
    { field: 'studentId', headerName: '학번', width: 110 },
    { field: 'email', headerName: '이메일', width: 180 },
    { field: 'phoneNumber', headerName: '전화번호', width: 130 },
    { field: 'department', headerName: '소속 학부/학과', width: 180 },
    { field: 'note', headerName: '비고', width: 280 }
  ]

  if (!club) {
    enqueueSnackbar('잘못된 접근입니다.', { variant: 'error' })
    return <Navigate to="/" />
  }

  return (
    <>
      <Box style={{ height: matches ? 670 : 480, width: '100%' }}>
        <DataGrid
          rows={members}
          columns={columns}
          rowSelectionModel={selectedMemberIds}
          onRowSelectionModelChange={handleRowSelectionChange}
          slots={{
            toolbar: () => (
              <CustomGridToolbar
                isAllSelectedMembersNotBelongToClub={isAllMembersNotBelongToClub}
                selectedMemberIds={selectedMemberIds}
                handleDeleteButtonClick={handleDeleteButtonClick}
                handleExportButtonClick={handleExportButtonClick}
                handleFireButtonClick={handleDeleteButtonClick}
                handleSearchButtonClick={handleSearchButtonClick}
              />
            )
          }}
          initialState={{
            columns: {
              columnVisibilityModel: {
                email: false,
                phoneNumber: false,
                studentId: window.innerWidth >= 600,
                level: window.innerWidth >= 600,
                isPaused: window.innerWidth >= 600,
                attendance: window.innerWidth >= 600,
                fee: window.innerWidth >= 600,
                note: window.innerWidth >= 600
              }
            }
          }}
          sx={{ bgcolor: 'white', borderRadius: matches ? '10px' : 0 }}
          autoPageSize
          checkboxSelection
          disableRowSelectionOnClick
        />
      </Box>
      <DetailsModal
        userPermission={club.permission}
        targetMembers={modalMembers}
        setTargetMembers={setModalMembers}
        handleUpdate={handleUpdatedMembers}
        existingMemberLevels={[]}
        clubId={clubId}
        isHonorary={true}
      />
      <ConfirmDialog
        open={isDeleteModal}
        handleClose={() => setIsDeleteModal(false)}
        handleSubmit={handleDeleteMembers}
        confirmMessage={`${selectedMembers.length}명의 회원을 삭제합니다. 이 작업은 되돌릴 수 없습니다.`}
      />
    </>
  )
}

export default HonoraryMemberGrid
